.content {
    height: 50%;
}
.content div {
    text-align: left;
    font-size: 1rem;
}
.content div > h1, .content div > h3 {
    text-align: center;
}
.geschichte {
    height: 100%;
}
.geschichte div {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 30px;
}
.geschichte > div > div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.geschichte > div > div > p {
    padding: 5px;
    border: 1px solid black;
    cursor: pointer;
    box-shadow: 2px 2px 2px #aaaaaa;
}
.geschichte > div > div > p:hover {
    background-color: black;
    color: white;
}
.geschichte img{
    width: 50px;
    height: 50px;
    box-shadow: 2px 2px 2px #aaaaaa;
}
ul {
    list-style-type: square;
}
.content div > div {
    padding-left: 10px;
    padding-right: 10px;
    text-align: left;
}
.content div > div > div {
    text-align:center;
}
.zeitplan li {
    list-style-type: none;
    padding-bottom: 5px;
}
